*{
    box-sizing: border-box;
}
.ludo-container{
    width: 450px;
    margin: 20px auto;
}
.ludo-container .ludo{
    height: 450px;
    width: 100%;
    background-image: url('./ludo-bg.jpg');
    background-size:contain ;
    position: relative;
}
.player-pieces{
    height: 100%;
    width: 10%;
}   
@keyframes spin {
    0% { transform: translate(50%,50%), rotate(0deg); }
    50%{ transform: translate(50%,50%), rotate(180deg) scale(1.4); }
    100% { transform: translate(50%,50%), rotate(360deg); }
  }
.player-piece.highlight{
    cursor:pointer;
    border:2px dashed;
    animation: spin 1s infinite linear;
    z-index: 1;
}
.player-piece{
    width: 3%;
    height: 3%;
    border:2px solid;
    border-radius:50px;
    position: absolute;
    transform: translate(50%,50%);
    z-index: 1;
}
[player-id="P1"].player-piece{
    background-color: #2eafff;
}
[player-id="P2"].player-piece{
    background-color:#049645;
}
.player-base{
    width: 40%;
    height: 40%;
    border: 30px solid;
    position:absolute;    
}
.player-bases [player-id="P1"].player-base{
    bottom: 0;
    left: 0;
    border-color: #2eafff;
}
.player-bases [player-id="P2"].player-base{
    top: 0;
    right: 0;
    border-color:#049645;
}
.player-base.highlight{
    animation:border-blink 0.7s infinite ease-in-out;
}

@keyframes  border-blink{
    50%{
        border-color:rgba(255,255,255,0.8);
    }
}
.btn{
    padding:8px 2px;
    border:none;
    cursor:pointer;
    font-size: 16px;
}
 
btn:disabled {
    opacity: 0.5;
}
.btn-dice{
    background-color: #009d60;
    color:white;
}
.row{
    display: flex;
    justify-content: space-between;
    margin-top: 15px;
}